g.dialog = null;
function login() {
  // write login functions here
  let app;
  if (Office.context.diagnostics.platform == "PC") {
    app = true;
  }
  Office.context.ui.displayDialogAsync(
    app ? `https://localhost:3000/login.html` : `${HOSTED_URL}/login.html`,
    { height: 62, width: 45, displayInIframe: true },
    function (asyncResult) {
      g.dialog = asyncResult.value;
      g.dialog.addEventHandler(Office.EventType.DialogMessageReceived, processMessage);
    }
  );
}
const processMessage = (arg) => {
  localStorage.setItem("beaconToken", arg.message);
  g.dialog.close();
  enableAllKeys();
  getTenantToken();
};
function logout() {
  let app;
  if (Office.context.diagnostics.platform == "PC") app = true;
  app = true;
  Office.context.ui.displayDialogAsync(
    app ? `https://localhost:3000/logout.html` : `${HOSTED_URL}/logout.html`,
    { height: 50, width: 35, displayInIframe: true },
    function (asyncResult) {
      g.dialog = asyncResult.value;
      g.dialog.addEventHandler(Office.EventType.DialogMessageReceived, logoutConfirm);
    }
  );
  const logoutConfirm = (arg) => {
    if (arg.message === "logout") {
      g.dialog.close();
      disableAllButtons();
      localStorage.removeItem("beaconToken");
      Office.addin.hide();
    }
    g.dialog.close();
  };
}
async function power(event) {
  Office.addin.hide();
  let beaconToken = localStorage.getItem("beaconToken");
  console.log(beaconToken, "beaconToken");
  if (beaconToken) {
    logout();
  } else {
    login();
  }
  event.completed();
}
